// Custom function to verify signature using SHA-256 and redirect if invalid
// Function to verify JWT using the secret key
document.body.innerHTML = ''; // Clear previous content
document.body.appendChild(splashWrapper); // Show splash screen
async function verifyJWT(token) {
    const secretKey = new TextEncoder().encode("G$8a9Zq5@dR3^yX!7wKj*Ue2#fH6%tLp");
    try {
        const { payload } = await jwtVerify(token, secretKey);
        console.log('Token is valid:', payload);
        // Continue with your application logic here
        document.body.innerHTML = '';
        document.body.appendChild(dashWrapper);
        handleDashboard();
    } catch (error) {
        console.error('Invalid token:', error);
        window.location.href = '/login'; // Redirect if invalid
    }
}

function getTokenFromUrl() {
    const urlParams = new URLSearchParams(window.location.search);
    return urlParams.get('token'); // Change 'token' if your query parameter has a different name

}
// Example usage
const token = getTokenFromUrl(); // Retrieve the token from the URL
if (token) {
    verifyJWT(token);
} else {
    window.location.href = '/login'; // Redirect if no token
}
